.page {
  font-family: $main-font;
  color: $main-text-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  scroll-behavior: smooth;
  overflow-x: hidden;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all $effect-duration;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
